:root {
  --font-body: "Open Sans", sans-serif;
  --font-heading: "Poppins", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --pink: hsl(322, 100%, 66%);
  --lt-pink: hsl(321, 100%, 78%);
  --lt-red: hsl(0, 100%, 63%);
  --very-dk-cyan: hsl(192, 100%, 9%);
  --very-pl-blue: hsl(207, 100%, 98%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--white);
  color: var(--very-dk-cyan);
  font-size: 0.875rem;
  font-weight: 400;
  text-align: center;
  line-height: 1.3;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-family: var(--font-heading);
  font-size: 3.3125rem;
  font-weight: 700;
  line-height: 1.5;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 2.5rem;
  max-width: 81.25rem;
  margin: 0 auto;
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 1.25rem;
  margin-bottom: 6.25rem;
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
}
@media (min-width: 40em) {
  .nav {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 0;
    margin-bottom: 6.875rem;
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
}
.nav__logo {
  display: inline-block;
  font-size: 0;
}
.nav__logo img {
  width: 8.75rem;
  height: 1.5rem;
}
@media (min-width: 40em) {
  .nav__logo img {
    width: 10.3125rem;
    height: 1.75rem;
  }
}
@media (min-width: 71.875em) {
  .nav__logo img {
    width: 14.6875rem;
    height: 2.3125rem;
  }
}
.nav__btn {
  display: inline-block;
  border: 0.0625rem solid;
  border-radius: 3.125rem;
  padding: 0.25rem 0.9375rem;
  font-size: 0.625rem;
  color: var(--lt-pink);
  transition: all 300ms ease-in-out;
}
.nav__btn:hover {
  border: 0.0625rem solid var(--lt-pink);
  background-color: var(--lt-pink);
  color: var(--white);
}
@media (min-width: 40em) {
  .nav__btn {
    border-radius: 3.125rem;
    padding: 0.625rem 1.875rem;
    font-size: 1rem;
  }
}

.getstarted {
  margin-bottom: 6.25rem;
}
@media (min-width: 40em) {
  .getstarted {
    margin-bottom: 6.875rem;
  }
}
.getstarted__header {
  font-family: var(--font-heading);
  font-weight: 700;
}
.getstarted__header--big {
  font-size: 2rem;
}
@media (min-width: 40em) {
  .getstarted__header--big {
    margin-bottom: 1.875rem;
    font-size: 2.25rem;
  }
}
@media (min-width: 71.875em) {
  .getstarted__header--big {
    font-size: 2.75rem;
  }
}
.getstarted__header--sm {
  font-size: 1.5625rem;
  margin-bottom: 1.875rem;
}
@media (min-width: 40em) {
  .getstarted__header--sm {
    font-size: 1.875rem;
  }
}
@media (min-width: 71.875em) {
  .getstarted__header--sm {
    font-size: 2.1875rem;
  }
}
.getstarted > p {
  margin-bottom: 3.75rem;
  line-height: 1.5;
}
@media (min-width: 40em) {
  .getstarted > p {
    max-width: 37.5rem;
    margin: 0 auto 3.75rem;
    font-size: 1.25rem;
  }
}
.getstarted > a {
  display: inline-block;
  padding: 1.125rem 3.125rem;
  border: 0.0625rem solid var(--pink);
  border-radius: 5rem;
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--white);
  background-color: var(--pink);
  transition: all 300ms ease-in-out;
}
.getstarted > a:hover {
  border: 0.0625rem solid var(--pink);
  color: var(--pink);
  background-color: var(--white);
}
@media (min-width: 40em) {
  .getstarted > a {
    padding: 1.25rem 5rem;
    border-radius: 5rem;
    font-size: 1.375rem;
  }
}
.getstarted-two {
  margin-bottom: 12.5rem;
}
@media (min-width: 40em) {
  .getstarted-two {
    margin-bottom: 18.75rem;
  }
}
@media (min-width: 71.875em) {
  .getstarted-two {
    margin-bottom: 21.875rem;
  }
}

.mockup {
  margin-bottom: 6.25rem;
}
@media (min-width: 40em) {
  .mockup {
    margin-bottom: 6.875rem;
  }
}
.mockup img {
  width: 100%;
}

.engagement {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5.625rem;
  margin-bottom: 9.375rem;
}
@media (min-width: 40em) {
  .engagement {
    flex-direction: row;
    justify-content: space-around;
    margin-bottom: 12.5rem;
  }
}
@media (min-width: 71.875em) {
  .engagement {
    margin-bottom: 18.75rem;
  }
}
.engagement__card {
  width: fit-content;
}
.engagement__icon {
  display: block;
  margin-bottom: 0.625rem;
}
.engagement__number {
  margin-bottom: 0.3125rem;
  font-family: var(--font-heading);
  font-size: 3.75rem;
  font-weight: 700;
}
@media (min-width: 40em) {
  .engagement__number {
    font-size: 5rem;
  }
}
@media (min-width: 71.875em) {
  .engagement__number {
    font-size: 6.25rem;
  }
}
.engagement__label {
  color: #919191;
}

.ft-section > :nth-child(odd) {
  background-color: var(--very-pl-blue);
}
.ft-section-grow {
  position: relative;
  margin-bottom: 12.5rem;
}
@media (min-width: 40em) {
  .ft-section-grow {
    margin-bottom: 15.625rem;
  }
}
@media (min-width: 71.875em) {
  .ft-section-grow {
    margin-bottom: 18.75rem;
  }
}
.ft-section-grow::before {
  content: "";
  background-image: url("../images/bg-section-top-mobile-1.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: absolute;
  left: 0;
  bottom: 100%;
  width: 100%;
  height: 9.375rem;
  z-index: -1;
}
@media (min-width: 40em) {
  .ft-section-grow::before {
    background-image: url("../images/bg-section-top-desktop-1.svg");
    width: 100%;
    height: 12.5rem;
  }
}
.ft-section-grow::after {
  content: "";
  background-image: url("../images/bg-section-bottom-mobile-1.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  height: 9.375rem;
  z-index: -1;
}
@media (min-width: 40em) {
  .ft-section-grow::after {
    background-image: url("../images/bg-section-bottom-desktop-1.svg");
    width: 100%;
    height: 12.5rem;
  }
}
.ft-section-flow {
  margin-bottom: 12.5rem;
}
@media (min-width: 40em) {
  .ft-section-flow {
    margin-bottom: 15.625rem;
  }
}
@media (min-width: 71.875em) {
  .ft-section-flow {
    margin-bottom: 18.75rem;
  }
}
.ft-section-users {
  position: relative;
  margin-bottom: 12.5rem;
}
@media (min-width: 40em) {
  .ft-section-users {
    margin-bottom: 15.625rem;
  }
}
@media (min-width: 71.875em) {
  .ft-section-users {
    margin-bottom: 18.75rem;
  }
}
.ft-section-users::before {
  content: "";
  background-image: url("../images/bg-section-top-mobile-2.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: absolute;
  left: 0;
  bottom: 100%;
  width: 100%;
  height: 9.375rem;
  z-index: -1;
}
@media (min-width: 40em) {
  .ft-section-users::before {
    background-image: url("../images/bg-section-top-desktop-2.svg");
    width: 100%;
    height: 12.5rem;
  }
}
.ft-section-users::after {
  content: "";
  background-image: url("../images/bg-section-bottom-mobile-2.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  height: 9.375rem;
  z-index: -1;
}
@media (min-width: 40em) {
  .ft-section-users::after {
    background-image: url("../images/bg-section-bottom-desktop-2.svg");
    width: 100%;
    height: 12.5rem;
  }
}

.feature {
  display: flex;
  flex-direction: column;
  gap: 5.625rem;
}
@media (min-width: 40em) {
  .feature {
    flex-direction: row;
    align-items: center;
    gap: 6.25rem;
  }
}
.feature__illus img {
  width: 100%;
}
@media (min-width: 40em) {
  .feature__illus--order {
    order: 2;
  }
}
@media (min-width: 40em) {
  .feature__illus {
    flex: 1;
  }
}
.feature__content h2 {
  font-size: 1.5625rem;
  font-family: var(--font-heading);
}
@media (min-width: 40em) {
  .feature__content h2 {
    font-size: 1.875rem;
  }
}
@media (min-width: 40em) {
  .feature__content p {
    font-size: 1rem;
  }
}
@media (min-width: 40em) {
  .feature__content--order {
    order: 1;
  }
}
@media (min-width: 40em) {
  .feature__content {
    flex: 1;
    text-align: left;
  }
}

footer {
  background-color: var(--very-dk-cyan);
  position: relative;
}
footer::before {
  content: "";
  background-image: url("../images/bg-footer-top-mobile.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: absolute;
  left: 0;
  bottom: 100%;
  width: 100%;
  height: 5rem;
  z-index: -1;
}
@media (min-width: 40em) {
  footer::before {
    background-image: url("../images/bg-footer-top-desktop.svg");
    width: 100%;
    height: 10.625rem;
  }
}

.footer {
  display: flex;
  flex-direction: column;
  gap: 5rem;
  padding-top: 5rem;
  padding-bottom: 3.125rem;
  color: var(--white);
  font-weight: 600;
  text-align: left;
}
.footer__logo {
  display: inline-block;
}
.footer__header {
  font-family: var(--font-heading);
  font-size: 1.25rem;
  text-transform: uppercase;
}
.footer__content {
  margin-bottom: 1.875rem;
  line-height: 1.8;
}
.footer-form {
  display: flex;
  flex-direction: column;
  gap: 0.625rem;
}
@media (min-width: 71.875em) {
  .footer-form {
    flex-direction: row;
    gap: 1.875rem;
  }
}
.footer__label {
  display: none;
}
.footer__email {
  border-radius: 0.3125rem;
  border: none;
  padding: 0.9375rem;
  font-size: 1rem;
}
@media (min-width: 71.875em) {
  .footer__email {
    flex: 2;
  }
}
.footer__submit {
  color: var(--white);
  background-color: var(--pink);
  border-radius: 0.3125rem;
  border: none;
  padding: 0.9375rem;
  font-size: 1rem;
  font-weight: 400;
  transition: background-color 300ms ease-in-out;
}
.footer__submit:hover {
  cursor: pointer;
  background-color: var(--lt-pink);
}
@media (min-width: 71.875em) {
  .footer__submit {
    flex: 1;
  }
}
@media (min-width: 40em) {
  .footer {
    flex-direction: row;
    gap: 6.25rem;
  }
  .footer-item1 {
    order: 2;
  }
  .footer-item2 {
    order: 1;
  }
}
.footer__phonenum {
  display: block;
  margin-bottom: 0.625rem;
}
.footer__phonenum::before {
  content: url("../images/icon-phone.svg");
  margin-right: 1.25rem;
}
.footer__emailadd {
  display: block;
  margin-bottom: 1.875rem;
}
.footer__emailadd::before {
  content: url("../images/icon-email.svg");
  margin-right: 1.25rem;
}
.footer__links {
  display: inline-block;
  font-size: 1.875rem;
  color: var(--white);
}
.footer__links:not(:last-child) {
  margin-right: 0.9375rem;
}